"use client"

import { useAuth } from "@/hooks/useAuth"
import { useRouter } from "next/navigation"
import { useEffect } from "react"
import Image from "next/image"

export default function AuthenticatedLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const { isAuthenticated, logout } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/")
    }
  }, [isAuthenticated, router])

  const handleLogout = () => {
    logout()
    router.push("/")
  }

  return (
    <div className="min-h-screen bg-gray-100">
      <header className="bg-blue-700 text-white shadow-lg">
        <div className="container mx-auto px-4 py-3 flex items-center justify-between">
          <div className="flex items-center space-x-8">
            <Image
              src="/caixa-logo.svg"
              alt="CAIXA"
              width={120}
              height={40}
              priority
            />
            <nav className="hidden md:flex space-x-6 text-sm">
              <a href="/dashboard" className="hover:text-blue-200 font-medium">Início</a>
              <a href="/dashboard/transferencia" className="hover:text-blue-200">Transferência</a>
              <a href="/dashboard/pagamentos" className="hover:text-blue-200">Pagamentos</a>
              <a href="/dashboard/extrato" className="hover:text-blue-200">Extrato</a>
            </nav>
          </div>
          <button 
            onClick={handleLogout} 
            className="text-sm hover:text-blue-200 font-medium"
          >
            Sair
          </button>
        </div>
      </header>

      <div className="bg-blue-800 text-white py-2">
        <div className="container mx-auto px-4">
          <div className="text-sm flex items-center justify-end space-x-4">
            <span>Última conexão: {new Date().toLocaleString('pt-BR')}</span>
            <span>|</span>
            <span>Agência: 1234</span>
          </div>
        </div>
      </div>

      <main className="container mx-auto px-4 py-8">
        {children}
      </main>

      <footer className="bg-gray-100 border-t border-gray-200 py-4 mt-auto">
        <div className="container mx-auto px-4 text-center text-sm text-gray-600">
          <p>© 2024 CAIXA - Todos os direitos reservados</p>
          <p className="mt-1">Central de Atendimento CAIXA: 0800 726 0101</p>
        </div>
      </footer>
    </div>
  )
}
